*{
    padding: 0;
    margin:0;
}

a{
    text-decoration: none;
}

body{
    background-color: 	#F5F5F5;
}

.header{
    width:100%;
    height: 50px;
    background-color: #FF6347;
    background-image: url(../assets/taobao.png);
    background-repeat: no-repeat;
    background-size: 50px;
    position: fixed;
    z-index: 1;
}
.searchBar{
    width: 300px;
    height: 30px;
    margin-top: 10px;    
    margin-left: 50px;
    border: 1px solid  	#FF6347;
    background-color: #FF4500;
    border-radius: 5px;
    text-align: center;
    color:white;
}

.lunbotu{
    width: 100%;
    height: 120px;
    padding-top: 50px;
}

.like{
    color: #FF4500;
    text-align: center;
    height: 30px;
    font-size: large;
    line-height: 30px;
}

.content{
    width:100%;
}

.contentList{
    border-radius: 10px;
    display: inline-block;
    margin: 0 auto;
    height: 200px;
    width: 170px;
    margin-left: 10px;
    background-color: white;
    background-repeat: no-repeat;
    background-size: 80%;
    background-position: 20px 10px;
    position: relative;
}

.contentList img{
    width: 100%;
    height: 100%;
    text-align: center;
}

.price{
    color:red;
    font-weight: bold;
}


.item{
    position: absolute;
    bottom: 0px;
    margin-left: 5px;
}

.bottombar{
    position: 'fixed';
    margin-top:230px;
    z-index: '2';
    width: '100%';
    height: '60px'
}

.log{
    margin-top: 200px;
    margin-left: 30px;
}

.log input{
    background-color: #F5F5F5;
    width: 300px;
    height: 30px;
    border-bottom:2px solid #FF4500;
    border-top:none;
    border-left: none;
    border-right: none;
    margin-top: 20px;
}

.log button{
    border:2px solid #ff4500;
    border-radius: 10px;
    width: 100px;
    height: 50px;
    background-color: #FF4500;
    margin-top: 30px;
    margin-left: 100px;
}